.b-video {
	margin: 0 0 30px;
	&__title {
		margin: 0 0 30px;
		font-weight: normal;
		text-align: center;
		strong {
			font-weight: 800;
		}
	}
	&__wrap {
		position: relative;
		&::before {
			content: '';
			display: block;
			padding-top: percentage(540/960);
			background: $colorBlack;
		}

		iframe {
			position: absolute;
			top: 0;
			left: 0;
			/* stylelint-disable declaration-no-important */
			width: 100% !important;
			height: 100% !important;
			border: none;
		}
	}
	&__video {
		&.rmp-container {
			position: absolute;
			top: 0;
			left: 0;
			width: 100% !important;
			height: 100% !important;
		}
		.rmp-i {
			font-family: rmp-fonts, sans-serif !important;
			font-size: 35px;
			&-pip {
				&::before {
					content: '\e904' !important;
					font-size: 35px;
				}
			}
			&-sharing {
				&::before {
					content: '\e906' !important;
				}
			}
			&-close {
				&::before {
					content: '\e90a' !important;
				}
			}
			&-airplay {
				&::before {
					content: '\e901' !important;
				}
			}
			&-play {
				&::before {
					content: '\e905' !important;
				}
			}
			&-pause {
				&::before {
					content: '\e903' !important;
				}
			}
			&-volume {
				&::before {
					content: '\e908' !important;
				}
			}
			&-off-volume {
				&::before {
					content: '\e907' !important;
				}
			}
			&-quick-rewind-10 {
				&::before {
					content: '\e900' !important;
				}
			}
			&-resize-full {
				&::before {
					content: '\e902' !important;
				}
			}
			&-resize-small {
				&::before {
					content: '\e909' !important;
				}
			}
		}
	}

	@media ($mdDown) {
		&__video {
			.rmp-title {
				height: 30px;
				line-height: 30px;
			}
			.rmp-module,
			.rmp-module-button {
				width: 30px;
				height: 30px;
				line-height: 30px;
			}
			.rmp-i {
				font-size: 30px;
				&-pip {
					&::before {
						font-size: 30px;
					}
				}
			}
			.rmp-mobile-central-ui {
				width: 50px;
				height: 50px;
				.rmp-i {
					font-size: 40px;
					line-height: 50px;
				}
			}
		}
	}
	@media ($lgUp) {
		&__title {
			font-size: 36px;
		}
	}
}
