.b-match {
	position: relative;
	padding-top: 23px;
	&__date {
		position: absolute;
		top: 0;
		left: 30px;
	}
	&__inner {
		display: flex;
		align-items: center;
		padding: 30px;
		background: rgba($colorBg, 0.95);
	}
	&__match {
		display: flex;
		flex: 1 1 auto;
		align-items: center;
		font-weight: bold;
		font-size: 17px;
		line-height: 23px;
	}
	&__team {
		display: flex;
		flex: 1 1 50%;
		align-items: center;
	}
	&__name {
		flex: 1 1 auto;
	}
	&__img {
		display: flex;
		flex: 0 0 78px;
		justify-content: center;
		align-items: center;
		width: 78px;
		height: 78px;
		margin: 0 15px 0 0;
		border-radius: 50%;
		background: $colorWhite;
		img {
			height: 58px;
			width: auto;
		}
	}
	&__score {
		display: flex;
		flex: 0 0 auto;
		align-items: center;
		padding: 0 15px;
		font-size: 40px;
		line-height: 1;
	}
	&__divider {
		margin: 0 20px;
	}
	&__btns {
		flex: 0 0 210px;
		margin: 0 0 0 50px;
		text-align: center;
		.btn {
			display: block;
		}
		.btn-link {
			margin-top: 20px;
		}
	}

	// VARIANTs
	&__team--away {
		text-align: right;
	}
	&__team--away &__img {
		order: 2;
		margin: 0 0 0 15px;
	}
	&--active &__inner {
		background: rgba(#28282e, 0.95);
	}

	/* stylelint-enable */
	@mixin tabletVersion {
		&__match {
			font-size: 14px;
			line-height: 18px;
			align-items: flex-start;
		}
		&__score {
			padding-top: 18px;
		}
		&__team {
			display: block;
			text-align: center;
		}
		&__img.b-match__img {
			margin: 0 auto 10px;
		}
	}
	@mixin mobileVersion {
		padding: 23px 0 0;
		&__wrap {
			background: rgba($colorBg, 0.95);
			padding: 1px 10px 30px;
		}
		&__date {
			position: static;
			margin: -24px 0 30px;
			&:first-child {
				margin-top: -23px;
			}
		}
		&__inner {
			display: block;
			padding: 0;
			background: none;
		}
		&__score {
			padding: 25px 10px 0;
			font-size: 30px;
		}
		&__divider {
			margin: -4px 7px 0;
		}
		&__btns {
			width: 210px;
			margin: 30px auto 0;
		}

		// VARIANTs
		&--active &__wrap {
			background: rgba(#28282e, 0.95);
		}
		&--active &__inner {
			background: none;
		}
	}

	.c-matches-grid & {
		@include tabletVersion;
		@include mobileVersion;
		&__wrap {
			background: rgba(#28282e, 0.95);
		}
	}
	/* stylelint-disable */

	@media ($lgDown) {
		@include tabletVersion;
	}

	@media ($mdDown) {
		@include mobileVersion;
	}
	@media ($lgUp) {
		.c-matches-grid & {
			&__score {
				font-size: 40px;
				padding-top: 18px;
			}
		}
	}
}
