.header {
	position: relative;
	z-index: 10;
	background: $colorPrimary;
	.row-main {
		position: static;
		max-width: $rowMainWidth - 2 * 48;
	}
	&__wrap {
		display: flex;
		justify-content: space-between;
	}
	&__logo {
		display: block;
		margin: 0;
		padding: 0;
		font-family: $fontPrimary;
		font-size: 0;
		line-height: 24px;
		a {
			display: block;
			padding: 32px 0;
		}
	}

	@media ($lgDown) {
		&__logo {
			img {
				width: 202px;
			}
		}
	}

	@media ($mdDown) {
		&__logo {
			a {
				padding: 18px 0;
			}
		}
	}
}
