.btn-link {
	display: inline-block;
	vertical-align: middle;
	padding: 0;
	border: 0;
	background: none;
	text-decoration: none;
	cursor: pointer;
	.item-icon .icon,
	.item-icon .icon-svg {
		margin-right: 10px;
	}
	&__text {
		position: relative;
		display: block;
		color: $colorPrimary;
		font-weight: bold;
		font-size: 14px;
		line-height: 20px;
		text-align: center;
		text-transform: uppercase;
		text-decoration: none;
		transition: color $t;
	}

	// VARIANTs
	// HOVERs
	.hoverevents &:hover &__text {
		color: $colorWhite;
	}
}
