.icon-svg {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	width: 28px;
	&__svg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		fill: currentColor;
		pointer-events: none;
		transform: translateZ(0);
	}
	&::before {
		content: '';
		display: block;
		padding-top: 100%;
	}

	
	&--facebook {
		width: 24px;
	} 
	&--football-calendar2 {
		width: 40px;
	} 
	&--football {
		width: 26px;
	} 
	&--instagram {
		width: 24px;
	} 
	&--menu-close {
		width: 38px;
	} 
	&--menu {
		width: 38px;
	} 
	&--separator {
		width: 12px;
	} 
	&--vs-dash {
		width: 37px;
	} 
	&--vs-dots {
		width: 6px;
	} 
	
	&--separator::before {
		padding-top: 591.6666666666667%;
	} 
	&--vs-dash::before {
		padding-top: 10.81081081081081%;
	} 
	&--vs-dots::before {
		padding-top: 333.33333333333337%;
	} 

}