.date {
	display: flex;
	line-height: 28px;
	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 46px;
		height: 46px;
	}
	&__content {
		padding: 9px 15px;
		background: $colorWhite;
		color: $colorBlack;
		box-shadow: 0 5px 20px 0 rgba($colorBlack, 0.1);
	}

	// VARIANTs
	&--soon &__content:not(&__content--highlight) {
		background: #ebeef4;
	}
	&--past &__content:not(&__content--highlight) {
		background: #b7bdca;
	}
	&__content--highlight {
		background: $colorPrimary;
		font-weight: bold;
	}

	/* stylelint-enable */
	@mixin mobileVersion {
		flex-wrap: wrap;
		justify-content: center;
		&__content {
			padding: 9px 13px;
		}
	}

	.c-matches-grid & {
		@include mobileVersion;
	}
	/* stylelint-disable */

	@media ($mdDown) {
		@include mobileVersion;
	}
}
