.c-matches {
	margin: 0 0 80px;
	&__title {
		width: 220px;
		margin: 0 auto -23px;
		padding: 25px 20px;
		background: rgba(#28282e, 0.65);
		text-align: center;
		text-transform: uppercase;
	}
	&__title-default {
		margin: 0 0 30px;
		text-align: center;
	}
	&__list {
		margin: 0 0 50px;
	}
	&__item {
		margin: 0 0 20px;
	}
	&__btn {
		text-align: center;
	}

	@media ($mdDown) {
		margin: 0 0 30px;
		&__title {
			width: 142px;
			margin: 0 auto;
			padding: 13px 20px;
			font-size: 18px;
		}
		&__title-default {
			margin: 0 0 20px;
		}
		&__list {
			margin: 0 0 30px;
		}
	}
}
