// Colors
$colorPrimary: #ffdb01;

$colorBlack: #000000;
$colorGrey: #b7bdca;
$colorWhite: #ffffff;
$colorRed: #e6003d;
$colorGreen: #49b435;

// $colorOrange: #ffaa00;

$colorText: $colorWhite;
$colorBd: #dddddd;
$colorBg: #171719;
$colorLink: $colorPrimary;
$colorHover: $colorWhite;

$colorFacebook: #3b5998;
$colorTwitter: #1da1f2;
$colorGoogle: #dd4b39;
$colorYoutube: #ff0000;
$colorLinkedIn: #0077b5;
$colorInstagram: #c13584;
$colorPinterest: #bd081c;

// Font
$fontSystem: -apple-system, blinkmacsystemfont, 'Segoe UI', roboto, helvetica, arial, sans-serif;
$fontPrimary: 'Open Sans', sans-serif;
$fontSecondary: $fontPrimary;
$fontSize: 14px;
$lineHeight: (20 / 14);

// Typography
$typoSpaceVertical: 1.25em;

// Grid
$gridColumns: 12;
$gridGutter: 20px;
$rowMainWidth: 1020px;
$rowMainGutter: 10px;

// Paths
$imgPath: '../img/';
$fontsPath: '../fonts/';

// Transitions
$t: 0.3s;

// SVGs
$svgBullet: "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 4'%3E%3Cpath d='M0 0h4v4H0z'/%3E%3C/svg%3E%0A";
$svgSelect: "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 10 5'%3E%3Cpath d='M10 0L5 5 0 0'/%3E%3C/svg%3E%0A";
