.btn {
	display: inline-block;
	vertical-align: middle;
	padding: 0;
	border: 0;
	background: none;
	outline: none;
	text-decoration: none;
	&__text {
		position: relative;
		display: block;
		display: flex;
		align-items: center;
		min-height: 56px;
		padding: 11px 20px;
		border-radius: 5px;
		background-color: $colorPrimary;
		color: $colorBlack;
		font-weight: 800;
		font-size: 14px;
		line-height: 16px;
		text-align: center;
		text-transform: uppercase;
		text-decoration: none;
		transition: background-color $t, border-color $t, color $t;
	}
	button#{&},
	&[href] {
		cursor: pointer;
	}

	// VARIANTs
	&--disabled &__text {
		min-height: 66px;
		background: rgba($colorWhite, 0.05);
		color: rgba($colorWhite, 0.8);
		font-weight: bold;
		text-align: left;
		text-transform: none;
	}
	&--bd &__text {
		height: 80px;
		padding: 10px 30px;
		border: 2px solid rgba($colorPrimary, 0.2);
		border-radius: 0;
		background: none;
		color: $colorPrimary;
		font-weight: bold;
		font-size: 23px;
		line-height: 25px;
		text-transform: none;
	}

	// STATEs
	&:disabled,
	&.is-disabled {
		opacity: 0.5;
		pointer-events: none;
	}

	// HOVERs
	.hoverevents button#{&}:hover &__text,
	.hoverevents &[href]:hover &__text {
		background-color: $colorWhite;
		color: $colorBlack;
	}

	@media ($mdDown) {
		&--bd &__text {
			padding: 0 23px;
			font-size: 18px;
			line-height: 24px;
		}
	}
}
