@mixin sprite2x-fortuna() {
	background: url($imgPath + 'bg/sprites-retina.png?v&#x3D;1607700389188') (0 / 2)+px (0 / 2)+px no-repeat;
	background-size: (32 / 2)+px (32 / 2)+px;
}
@mixin sprite2x-fortuna-size() {
	@include sprite2x-fortuna();
	width: (32 / 2)+px;
	height: (32 / 2)+px;
}
@mixin sprite2x-fortuna-position() {
	background-position: (0 / 2)+px (0 / 2)+px;
}
