.main {
	position: relative;
	min-height: 690px;
	background: url($imgPath+'bg/main.jpg') 50% 0 no-repeat;

	@media ($lgDown) {
		&__bg {
			display: none;
		}
	}

	@media ($mdDown) {
		background-image: url($imgPath+'bg/main-mobile.jpg');
		background-size: 100% auto;
	}

	@media ($lgUp) {
		&__bg {
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			height: 950px;
			overflow: hidden;
			img {
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
			}
			+ .row-main {
				min-height: 690px;
				background: $colorBlack url($imgPath+'bg/main.jpg') 50% 0 no-repeat;
			}
		}
	}
}
