.footer {
	margin: 0 0 60px;
	&__wrap {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	// VARIANTs
	&__item--separator {
		margin: 0 40px;
	}

	@media ($smDown) {
		margin: 0;
		&__wrap {
			flex-wrap: wrap;
		}
		&__item {
			flex: 0 0 auto;
			width: 100%;
			margin: 0 0 60px;
		}

		// VARIANTs
		&__item--separator {
			display: none;
		}
	}
}
