.m-main {
	&__list {
		@extend %reset-ul;
		text-transform: uppercase;
	}
	&__item {
		@extend %reset-ul-li;
	}
	&__link {
		position: relative;
		display: block;
		color: rgba($colorBlack, 0.5);
		text-decoration: none;
		&::before {
			content: '';
			position: absolute;
			height: 2px;
			background: $colorBlack;
			transition: left $t, right $t, opacity $t;
		}
	}

	// STATEs
	&__link.is-active,
	.hoverevents &__link:hover {
		color: $colorBlack;
		&::before {
			right: 0;
			left: 0;
			opacity: 1;
		}
	}

	@media ($mdDown) {
		&__tool {
			position: absolute;
			top: 11px;
			right: 15px;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 38px;
			height: 38px;
			border-radius: 4px;
			transition: background-color $t;
			.icon-svg {
				flex: 0 0 auto;
				transition: opacity $t;
				&--menu-close {
					opacity: 0;
				}
				+ .icon-svg {
					margin-left: -100%;
				}
			}
		}
		&__list {
			position: absolute;
			top: 100%;
			right: 0;
			left: 0;
			padding: 0 15px;
			background: $colorPrimary;
			font-weight: bold;
			font-size: 16px;
			text-align: right;
			visibility: hidden;
			opacity: 0;
			transition: opacity $t, visibility 0s $t;
			&::after {
				content: '';
				position: absolute;
				top: 100%;
				right: 0;
				left: 0;
				height: 20px;
				background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
				opacity: 0.3;
			}
		}
		&__link {
			padding: 15px 0;
			&::before {
				top: 0;
				right: 0;
				left: 0;
				opacity: 0.1;
			}
		}

		// STATEs
		.menu-open &__list {
			visibility: visible;
			opacity: 1;
			transition-delay: 0s, 0s;
		}
		.menu-open &__tool {
			.icon-svg {
				&--menu {
					opacity: 0;
				}
				&--menu-close {
					opacity: 1;
				}
			}
		}
	}

	@media ($mdUp) {
		&__tool {
			display: none;
		}
		&__list {
			display: flex;
			font-weight: bold;
			font-size: 14px;
			line-height: 1;
		}
		&__item {
			margin-left: 20px;
		}
		&__link {
			padding: 37px 0;
			&::before {
				right: 50%;
				bottom: 19px;
				left: 50%;
			}
		}
	}
}
